function extractClasses(json, className) {
    let classes = Array();
    let newClass = {
        name: className,
        variables: [],
    };
    Object.keys(json).forEach((key) => {
        let name, type;
        name = key;
        switch (typeof json[key]) {
            case "string":
                type = "String";
                break;
            case "boolean":
                type = "bool";
                break;
            case "number":
                if (json[key] % 1 === 0) {
                    type = "int";
                    break;
                }
                else {
                    type = "double";
                    break;
                }
            default:
                // Case complex type
                const className = key.charAt(0).toUpperCase() + key.slice(1);
                type = key.charAt(0).toUpperCase() + key.slice(1);
                classes = [...extractClasses(json[key], className), ...classes];
                break;
        }
        newClass.variables.push({ name, type });
    });
    classes.push(newClass);
    return classes;
}
function writeClass(dClass) {
    return `class ${dClass.name} {
${dClass.variables.map((v) => `  final ${v.type} ${v.name};`).join("\n")}
            
  const ${dClass.name}({
${dClass.variables.map((v) => `    required this.${v.name};`).join("\n")}
  });
          
  factory ${dClass.name}.fromJson(Map<String, dynamic> json) {
    return ${dClass.name}(
${dClass.variables
        .map((v) => `      ${v.name}: json['${v.name}'] as ${v.type},`)
        .join("\n")}
    );
  }
          
  Map<String, dynamic> toJson() {
    return {
${dClass.variables.map((v) => `      '${v.name}': this.${v.name},`).join("\n")}
    };
  }
}
          
`;
}
export function convert2Dart(rawInput, className = "Generated") {
    const jsonObj = JSON.parse(rawInput);
    const classes = extractClasses(jsonObj, className);
    let result = "";
    for (let i = 0; i < classes.length; i++) {
        result += writeClass(classes[i]);
    }
    return result;
}
